.contacts {
  max-width: 1190px;
  min-height: 700px;
  padding-left: 1rem;
  padding-right: 1rem;
  margin: 0 auto;
  text-align: center;

  @media (max-width: $tablet-width) {
//    max-width: 710px;
    min-height: 470px;
    padding-left: 4.5rem;
  padding-right: 4.5rem;
  }

  @media (max-width: $mobile-width) {
    min-height: 565px;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }

}

.contacts span {
 margin-right: 0.5rem;

}

.contacts h2 {
  font-size: 34px;
line-height: 130%;
  letter-spacing: 0.03em;
  color: $text-black;
  max-width: 20rem;
  margin: 0 auto;
  margin-bottom: 3rem;

  @media (max-width: $mobile-width) {
    margin-bottom: 2rem;
  }
}

.contacts a {
  border-bottom: 1px solid $primary;
  font-size: 16px;
line-height: 130%;
}
.contacts__wrapper {
  display: grid;
//grid-template-columns: repeat(4, 1fr);
  grid-template-columns: 1fr 1.7fr 1fr 1fr;
grid-template-rows: repeat(2, 1fr);
grid-column-gap: 22px;
grid-row-gap: 30px;
  margin-top: 6.5rem;

  @media (max-width: $tablet-width) {
    grid-column-gap: 16px;
    grid-row-gap: 1.5rem;
    margin-top: 6rem;
  }

  @media (max-width: $mobile-width) {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: min-content min-content min-content;
    margin-top: 5rem;
    grid-row-gap: 2rem;
  }

}

.contacts__item:first-child {
  @media (max-width: $tablet-width) {
    min-width: 133px;
    min-height: 133px;
  }
}

.contacts__item:nth-child(2n) {
  grid-row: span 2;
  grid-row: 1 / 3;
  grid-column: 2;

  @media (max-width: $tablet-width) {
    min-width: 133px;
    min-height: 133px;
  }

  @media (max-width: $mobile-width) {
//    grid-row: 1 / 3;
    grid-row: span 2;


  }
}

.contacts__item:nth-child(3n) {

@media (max-width: $mobile-width) {
    display: none;
  }

}

.contacts__item:nth-child(4n) {
  grid-row: 1;
  grid-column: 4;

  @media (max-width: $mobile-width) {
    display: none;
  }
}

.contacts__item:nth-child(5n) {

  @media (max-width: $mobile-width) {
    grid-row: 2;
    grid-column: 1;
  }
}

.contacts__item:nth-child(6n) {
  grid-row: 2;
  grid-column: 3;

  @media (max-width: $mobile-width) {
    display: block;
    grid-row: 3;
    grid-column: 1;
  }
}

.contacts__item:nth-child(7n) {
  grid-row: 2;
  grid-column: 4;

  @media (max-width: $mobile-width) {
    grid-row: 3;
    grid-column: 2;
  }
}

.contacts__item img {
  width: 100%;
  height: auto;
  max-width: 230px;
    object-fit: cover;

}

.contacts__item img,
.contacts__item:nth-child(4n)
{
  width: 100%;
  height: auto;




}

